imation:gearIntro 1s;animation:gearIntro 1s}.top-card .gear-holder .gear-holder-left{height:100%;width:18%;display:flex;align-items:flex-end}.top-card .gear-holder .gear-holder-left .left-gear{-webkit-animation:rotate 2s infinite;animation:rotate 2s infinite}.top-card .gear-holder .gear-holder-middle{height:100%;width:57%}.top-card .gear-holder .gear-holder-middle .middle-gear-wrapper{position:relative}.top-card .gear-holder .gear-holder-middle .middle-gear-wrapper .middle-gear{position:absolute;-webkit-animation:rotate 2.5s infinite;animation:rotate 2.5s infinite}.top-card .gear-holder .gear-holder-middle .middle-gear-wrapper .small-middle-gear{position:absolute;-webkit-filter:invert(0.8);filter:invert(0.8);-webkit-animation:rotateForSmaller 4s infinite;animation:rotateForSmaller 4s infinite}.top-card .gear-holder .gear-holder-middle .middle-gear-wrapper .smaller-middle-gear{position:absolute;right:5px;top:60px;-webkit-filter:invert(0.8);filter:invert(0.8);-webkit-animation:rotateForSmaller 10s infinite;animation:rotateForSmaller 10s infinite}.top-card .gear-holder .gear-holder-right{height:100%;position:relative}.top-card .gear-holder .gear-holder-right .right-gear{position:absolute;left:-18px;top:-10px;-webkit-animation:rotate 3s infinite;animation:rotate 3s infinite}.clip-background-color .path-clip-border{height:2.2rem;-webkit-clip-path:polygon(50% 100%, 0 0, 100% 0);clip-path:polygon(50% 100%, 0 0, 100% 0);background:rgba(105,67,67,0.767)}.clip-background-color .path-clip{background:#f97070;-webkit-clip-path:polygon(50% 100%, 0 0, 100% 0);clip-path:polygon(50% 100%, 0 0, 100% 0);height:2rem}.parallax{position:relative}.firstCard{width:100%;height:720px;display:flex;flex-direction:column;align-items:center}.firstCard .msg-title{margin:0;margin-top:30px;text-align:center;font-size:4rem}.firstCard .list-container{margin:0;padding:0;list-style:none;text-align:center}.firstCard .list-container .list{padding:50px;border:1px solid #5f5f5f;margin:20px 0;background-color:rgba(255,244,224,0.363)}.firstCard .list-container .list .title-logo{margin:0;padding:0;font-size:3rem;-webkit-mask-image:linear-gradient(-75deg, #fff 49%, rgba(0,0,0,0.021) 50%, #fff 58%);mask-image:linear-gradient(-75deg, #fff 49%, rgba(0,0,0,0.021) 50%, #fff 58%);-webkit-mask-size:200%;mask-size:200%;-webkit-animation:shine 3s linear infinite;animation:shine 3s linear infinite;text-align:center;text-shadow:2px 2px 3px gray}.clip-background-color-two .path-clip-border-two{-webkit-clip-path:polygon(50% 0, 0 100%, 100% 100%);clip-path:polygon(50% 0, 0 100%, 100% 100%);height:2rem;background:rgba(105,67,67,0.767)}.clip-background-color-two .path-clip-two{background:#f97070;-webkit-clip-path:polygon(50% 12%, 0 100%, 100% 100%);clip-path:polygon(50% 12%, 0 100%, 100% 100%);height:2rem}.second-card{background:#f97070;display:flex;flex-direction:column;justify-content:center;align-items:center}.second-card .skill-title{margin:0;font-size:5rem}.second-card .skill-logos-container{display:flex;flex-wrap:wrap;align-items:center;justify-content:center;width:200px}.second-card .skill-logos-container>img{padding:10px;background-image:radial-gradient(rgba(206,255,183,0.602) 10%, rgba(0,0,255,0) 67%)}.comp-footer{text-align:center;color:black;background-color:rgba(255,237,203,0.733);height:70px;border-top:3px solid black;display:flex;justify-content:center;align-items:center}@media only screen and (max-width: 434px){.top-card{height:335px}}

